window.application = {
    gameLevels: { 1: 6, 2: 12, 3: 18 },
    chosenCards: [],
    chosenCardsQuantity: 0,
    points: 0,
    gameResultFunction: function () {
        window.application.points ===
            window.application.chosenCardsQuantity / 2
            ? alert("Вы выиграли")
            : alert("Вы проиграли");
    },
};
export {};
